import React, { useMemo } from 'react';
import moment from 'moment';
import Header from '../header/Header.js';
import { CircularProgress, Grid, Typography } from '@material-ui/core';
import useAuctionData from '../../hooks/useAuctionData.js';
import { useSortBy, useTable, usePagination } from 'react-table';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import TableControls from './TableControls.js';

const Auctions = () => {
    const { auctions, countdown, lastUpdated } = useAuctionData();
    const {
        rows,
        page,
        gotoPage,
        nextPage,
        pageCount,
        prepareRow,
        setPageSize,
        pageOptions,
        canNextPage,
        previousPage,
        headerGroups,
        canPreviousPage,
        state: { pageIndex, pageSize }
    } = useTable({
        initialState: {
            pageIndex: 0, 
            pageSize: 10, 
            sortBy: [{ id: 'id', desc: true }] 
        },
        data: useMemo(() => auctions || [], [auctions]),
        columns: useMemo(() => [
            { Header: 'Auction ID', accessor: 'id', width: 2 },
            { Header: 'Start Date', accessor: 'start', width: 2 },
            { Header: 'End Date', accessor: 'end', width: 2 },
            { Header: 'AXN in Pool', accessor: 'axn', width: 2 },
            { Header: 'ETH in Pool', accessor: 'eth', width: 2 },
        ], []),
    }, useSortBy, usePagination)

    const tableProps = {
        rows,
        page,
        gotoPage,
        pageSize,
        nextPage,
        pageIndex,
        pageCount,
        setPageSize,
        pageOptions,
        canNextPage,
        previousPage,
        canPreviousPage,
    }

    return (
        <div>
            <Header/>
            <div style={{ padding: '2%', paddingBottom: 0, margin: '0 auto' }}>  
                {/* H E A D E R */}
                {headerGroups.map(({ headers }) => (
                    <Grid container key="container" justify="space-between" className="tableControls">
                        {headers.map((header, idx) => (
                            <Grid item container direction="row" xs={header.width} key={header + " " + idx} alignItems="center">
                                <Grid item>
                                    <Typography variant="subtitle1" {...header.getHeaderProps(header.getSortByToggleProps())} style={{color: '#FFF', cursor: 'pointer'}}>{header.Header}</Typography>
                                </Grid>
                                <Grid item>
                                    {header.isSorted
                                        ? header.isSortedDesc
                                            ? <KeyboardArrowDownIcon style={{ fontSize: '15px', marginLeft: '5px', color: "#FFF" }} />
                                            : <KeyboardArrowUpIcon style={{ fontSize: '15px', marginLeft: '5px', color: "#FFF" }} />
                                        : ''}
                                </Grid>
                            </Grid>
                        ))}
                    </Grid>
                ))}
          
                {/* A U C T I O N S */}
                {auctions.length === 0 && (
                    <div style={{margin: '5% auto'}}>
                        <center>
                            <CircularProgress color="primary" size={30} />
                        </center>
                    </div>
                )}

                {auctions.length > 0 && page.map(row => {
                    prepareRow(row)
                    return (
                        <div key={row.id} className="auctionTableRow" style={{ backgroundColor: row.original.isWeekly ? "rgba(103, 126, 218, 0.35)" : 'rgba(103, 126, 218, 0.1)'}}>
                            <Grid container justify="space-between" key={row.id} alignItems="center">
                                <Grid item xs={2}>
                                    <Typography color="textPrimary" variant="subtitle2">{row.values.id} </Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography color="textPrimary" variant="subtitle2">{moment.unix(row.values.start).format("MMM Do, YYYY h:mm:ss a")}</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography color="textPrimary" variant="subtitle2">{moment.unix(row.values.end).format("MMM Do, YYYY h:mm:ss a")}</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography color="textPrimary" variant="subtitle2">{row.values.axn.toLocaleString()}</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography color="textPrimary" variant="subtitle2">{row.values.eth.toLocaleString()}</Typography>
                                </Grid>
                            </Grid>
                        </div>
                    )
                })}

                <TableControls {...tableProps} />

            </div>
        </div>
    )
}

export default Auctions;